import React from 'react'

export const MarvelLogoIcon = ({ color = '#404040', width = 326, height = 120 }) => {
  const [fatorConversaoImageX, fatorConversaoImagemY] = [219 / 326, 120 / 120]
  const [fatorConversaoTextoX, fatorConversaoTextoY] = [219 / 326, 95 / 120]
  return (
    <svg
      width={width}
      height={height}
      viewBox={`0 0 ${width} ${height}`}
      version="1.1"
      xmlns="http://www.w3.org/2000/svg"
    >
      <g id="Page-1" stroke="none" strokeWidth="1" fill="none" fillRule="evenodd">
        <g id="Guide" transform="translate(-65.000000, -161.000000)">
          <g id="logo" transform="translate(65.000000, 161.000000)">
            <image
              id="Bitmap"
              x="0"
              y="0"
              width={width * fatorConversaoImageX}
              height={height * fatorConversaoImagemY}
              xlinkHref="data:image/png;base64,"
            ></image>
            <text id="Search-heros" fontFamily=".AppleSystemUIFont" fontSize="16.672" fontWeight="500" fill={color}>
              <tspan x={width * fatorConversaoTextoX} y={height * fatorConversaoTextoY}>
                Search heros
              </tspan>
            </text>
          </g>
        </g>
      </g>
    </svg>
  )
}
